import * as THREE from 'three'
export const ObjectSelect = (_scene, _camera, outlinePass, _orbitControl, fun, cameraPosition, controlTarget) => {
    var raycaster = new THREE.Raycaster();
    var mouse = new THREE.Vector2();
    var selectedObjects = [];
    /****************************************************************************************************************************
     * *
     * @type {THREE.OutlinePass}
     */

    window.addEventListener('click', onMouseClick);
    window.addEventListener('dblclick', onMouseDbClick);
    window.addEventListener('mousemove', onDocumentMouseMove)
    function onMouseClick (event) {
        var x, y;
        if (event.changedTouches) {
            x = event.changedTouches[0].pageX;
            y = event.changedTouches[0].pageY;
        } else {
            x = event.clientX;
            y = event.clientY;
        }
        mouse.x = (x / window.innerWidth) * 2 - 1;
        mouse.y = -(y / window.innerHeight) * 2 + 1;
        raycaster.setFromCamera(mouse, _camera);
        var intersects = raycaster.intersectObjects([_scene], true);

        if (intersects.length == 0) {
            return;
        }
        let coordinate
        if (intersects[0] && intersects[0].point) {
            coordinate = intersects[0].point
            console.log(coordinate, '')
        }
    }

    function onMouseDbClick (event) {
        var x, y;
        x = event.clientX;
        y = event.clientY;
        mouse.x = (x / window.innerWidth) * 2 - 1;
        mouse.y = -(y / window.innerHeight) * 2 + 1;
        raycaster.setFromCamera(mouse, _camera);
        var intersects = raycaster.intersectObjects([_scene], true);
        if (intersects.length == 0) {
            hideOtherFloor(true); // 
        } else { // 
            hideOtherFloor(false, intersects[0].object.parent.name)
        }
    }
    function onDocumentMouseMove (event) {
        console.log(_camera.position, _orbitControl.target, '')
        var x, y;
        if (event.changedTouches) {
            x = event.changedTouches[0].pageX;
            y = event.changedTouches[0].pageY;
        } else {
            x = event.clientX;
            y = event.clientY;
        }
        mouse.x = (x / window.innerWidth) * 2 - 1;
        mouse.y = -(y / window.innerHeight) * 2 + 1;
        raycaster.setFromCamera(mouse, _camera);
        var intersects = raycaster.intersectObjects([_scene], true);

        if (intersects.length == 0) {
            return;
        }
        if (intersects[0].object.name == "") {
            // selectedObjects.pop();
        } else {
            selectedObjects.pop();
            selectedObjects.push(intersects[0].object.parent);
            outlinePass.selectedObjects = selectedObjects;//
            let name = intersects[0].object.parent.parent.name
            if (name.includes('smcyy_jmkl_f16_')) { // 
                outlinePass.visibleEdgeColor.set('#e75f58') // 
            } else {
                outlinePass.visibleEdgeColor.set(parseInt('0x00FFFF')) // 
            }
        }

    }

    // false    true
    function hideOtherFloor (show, name) {
        const building = _scene.getObjectByName(
            "smcyy_jmkl"
        ); // 
        // eslint-disable-next-line no-debugger
        if (show) { // 
            // 
            // 1.
            const floorList = building.children; // 
            _scene.getObjectByName("AGV").visible = false
            floorList.map((el) => {
                el.visible = true;
            })
            fun(cameraPosition[0], cameraPosition[1], cameraPosition[2], controlTarget[0], controlTarget[1], controlTarget[2])
        } else {
            // 1.
            const floorList = building.children; // 
            if (name.includes('smcyy_jmkl_f16_')) { // 
                _scene.getObjectByName("AGV").visible = true
            } else {
                _scene.getObjectByName("AGV").visible = false
            }
            floorList.map((el) => {
                if (el.name !== name) {
                    el.visible = false;
                }
            });
            if (name === '9c001' || name === '8c001' || name === '7c001' || name === '6c001') {
                fun(76.90097560862407, 2001.4524009301272, -198.43114513307248, 76.89897485376429, 1.45240093113044, -198.4311454917692)
            } else if (name === '11c001' || name === '10c001') {
                fun(76.90097560862407, 2001.4524009301272, -198.43114513307248, 76.89897485376429, 1.45240093113044, -198.4311454917692)
            } else {
                fun(76.90097560862407, 2001.4524009301272, -198.43114513307248, 76.89897485376429, 1.45240093113044, -198.4311454917692)
            }
            console.log(_camera, '')
            console.log(_orbitControl, '')
        }
    }
}